<template>
  <div>
    <div class="user-info">
      <textarea placeholder="收货信息" type="textarea" rows="4" v-model="raw_info" class="form-textarea"></textarea>
      <div class="splited-info" v-show="raw_info">
        <input placeholder="姓名" v-model="splited_info.user_name" class="form-input" />
        <input placeholder="电话" v-model="splited_info.user_phone" class="form-input" />
        <input placeholder="省" v-model="splited_info.porvince" class="form-input"/>
        <input placeholder="市" v-model="splited_info.city" class="form-input"/>
        <input placeholder="区" v-model="splited_info.country" class="form-input"/>
        <textarea placeholder="街道" v-model="splited_info.street" class="form-input"/>
      </div>
    </div>
    <div class="mark-content">
      <textarea placeholder="备注" v-model="remark" rows="5" class="form-textarea"></textarea>
    </div>
    <div class="item-list">
      <div class="item-box" v-for="(item,index) in items" :key="index">
        <div class="box-title">
          产品{{index + 1}}
          <div class="remove-btn" @click="removeItem(index)">移除</div>
        </div>
        <input placeholder="产品名" v-model="item.name" class="form-input"/>
        <input placeholder="分类" v-model="item.type" class="form-input"/>
        <input placeholder="颜色" v-model="item.color" class="form-input"/>
        <input placeholder="型号" v-model="item.size" class="form-input"/>
        <input placeholder="数量" v-model="item.num" type="number" class="form-input"/>
        <input placeholder="售价" v-model="item.price" type="number" class="form-input"/>
        <input placeholder="取货价" v-model="item.cost" type="number" class="form-input"/>
        <input placeholder="货号" v-model="item.sku_no" class="form-input"/>
        <input placeholder="厂家" v-model="item.supplier" class="form-input"/>
        <input placeholder="取货地址" v-model="item.address" class="form-input"/>
        <input placeholder="备注" v-model="item.remark" class="form-input"/>
      </div>
      <div class="item-btn">
        <button type="default" size="large" plain @click="addItem">添加产品</button>
      </div>
    </div>
    <button type="primary" size="large" plain @click="createOrder" class="submit-btn">创建订单</button>
  </div>
</template>

<script>

const wx = require('weixin-js-sdk');

class Item {
  constructor() {
    this.name = ''
    this.type = ''
    this.size = ''
    this.num = ''
    this.remark = ''
    this.price = ''
    this.sku_no = ''
    this.cost = ''
    this.color = ''
    this.supplier = ''
    this.address = ''
    this.images = []
  }
}

export default {
  name: 'OrderForm',
  data: () => {
    return {
      raw_info: '',
      splited_info: {
        user_name: '',
        user_phone: '',
        porvince: '',
        city: '',
        country: '',
        street: '',
      },
      remark: '',
      items: [],
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    addItem () {
      this.items.push(new Item())
    },
    removeItem (index) {
      if(confirm('确认移除该产品吗？')) {
        this.items.splice(index, 1)
      }
    },
    createOrder () {
      if (this.splited_info.user_name && this.splited_info.user_phone) {
        if (confirm('确定创建订单吗？')) {
          const orderInfo = {
            ...this.splited_info,
            raw_info: this.raw_info,
            items: this.items.map(item => {
              return {
                ...item,
                price: item.price * 100,
                cost: item.cost * 100,
              }
            }),
            remark: this.remark,
          }
          let myHeaders = new Headers();
          myHeaders.append('Content-Type', 'application/json');
          fetch('/shooop/api/order/add', {
            method: 'POST',
            body: JSON.stringify(orderInfo),
            headers: myHeaders,
          })
          .then(res => res.json())
          .then(() => {
            wx.miniProgram.getEnv(ress => {
              if(ress.miniprogram) {
                wx.miniProgram.navigateTo({
                  url: `/pages/orderList/index`
                })
              }
            })
          })
        }
      } else {
        alert('请填写收货信息')
      }
    }
  },
  watch: {
    raw_info (val) {
      let splited_info = null
      if(/自治区/g.test(val)) {
        splited_info = /^(\D*)?(\d{11})?(.+?自治区)?(.+?市)?(.+?区)?(.*)?$/g.exec(val)
      } else {
        splited_info = /^(\D*)?(\d{11})?(.+?[省|北京|上海|重庆|天津])?(.+?市)?(.+?区)?(.*)?$/g.exec(val)
      }
      if (splited_info) {
        this.splited_info = {
          user_name: splited_info[1] && splited_info[1].trim(),
          user_phone: splited_info[2] && splited_info[2].trim(),
          porvince: splited_info[3] && splited_info[3].trim(),
          city: splited_info[4] && splited_info[4].trim(),
          country: splited_info[5] && splited_info[5].trim(),
          street: splited_info[6] && splited_info[6].trim(),
        }
      }
    }
  }
}
</script>

<style lang="scss">
.form-textarea {
  border: 1px solid #595959;
  display: block;
  width: 100%;
  font-size: 16px;
  box-sizing: border-box;
  padding: 5px;
}
.mark-content {
  margin: 15px 0;
}
.form-input {
  outline:none;
  border: none;
  border-bottom: 1px solid #595959;
  border-radius: 0;
  display: block;
  width: 100%;
  padding: 10px 5px;
  font-size: 16px;
  box-sizing: border-box;
  &:focus {
    outline:none;
    border: none;
    border-bottom: 1px solid #595959;
  }
}
.item-list {
  .item-box {
    margin-top: 10px;
    border-top: 5px solid #e8e8e8;
    .box-title {
      text-align: left;
      padding: 5px 10px;
      border-bottom: 1px solid #595959;
      .remove-btn {
        float: right;
        color: #f5222d;
      }
    }
  }
  .item-btn {
    margin-top: 10px;
    margin-bottom: 50px;
    button {
      box-sizing: border-box;
      padding: 6px 12px;
      background-color: #fff;
      border: 1px solid #000;
      font-size: 16px;
      width: 100%;
    }
  }
}
.submit-btn {
  margin: 50px 0;
  padding: 6px 12px;
  background: #fff;
  border: 1px solid #1890ff;
  color: #1890ff;
  box-sizing: border-box;
  font-size: 16px;
  width: 100%;
}
</style>


